.home-body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 128px;
  padding: 64px;
}

.movie-container {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  height: 700px;
  padding: 32px;
  transition: all 0.2s ease-in-out;
  .movie-main {
    display: flex;

    .movie-genres {
      display: flex;
      .movie-genre {
        margin-top: 12px;
        margin-right: 8px;
        font-weight: 600;
        color: rgba($color: #000000, $alpha: 0.4);
      }
    }
    .movie-main_text {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      margin-left: 24px;
      .movie-title {
        font-size: 56px;
        font-weight: 800;
        .movie-title_link {
          color: inherit;
          text-decoration: none;
        }
      }
    }
  }
  .movie-story {
    margin-top: 24px;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.2;
    color: rgba($color: #000000, $alpha: 0.8);
  }
  &:hover {
    background-color: rgba($color: #000000, $alpha: 0.8);
    .movie-main {
      color: whitesmoke;
      img {
        box-shadow: rgba(100, 100, 111, 0.8) 0px 7px 29px 0px;
      }
      .movie-genre {
        color: whitesmoke;
      }
    }
    .movie-story {
      color: whitesmoke;
    }
  }
}
