.detail-container {
  display: flex;
  height: 100vh;
  background-color: rgba($color: #161616, $alpha: 1);
  color: whitesmoke;
  .detail-img {
    height: 100%;
  }
  .detail-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 40px;
    margin-bottom: 64px;
    .detail-title {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      margin-bottom: 56px;
      .detail-link {
        text-decoration: none;
        font-size: 80px;
        font-weight: 800;
        color: inherit;
        margin-bottom: 12px;
      }
      .detail-genre {
        font-size: 20px;
        font-weight: 600;
      }
      .detail-genre:not(:last-child) {
        border-right: 0.5px solid #888888;
        margin-right: 12px;
        padding-right: 12px;
      }
    }
    .detail-story {
      font-size: 16px;
      font-weight: 300;
      width: 80%;
      line-height: 1.4;
      color: rgba($color: #cfcfcf, $alpha: 1);
    }
  }
}
